import { Injectable } from '@angular/core';
import { AppListItem } from './app-list/app-list-item';
import { AppListData } from './app-list/app-list-data';
@Injectable()
export class AppInfoService {

    constructor() { }
  getAppList(): Promise<AppListItem[]> {
      return Promise.resolve(AppListData);
  }
}



