import { Component, Input, HostListener } from '@angular/core';
import { AppListItem } from '../action-list/app-list/app-list-item';
@Component({
  moduleId: module.id,
  selector: 'app-item',
  templateUrl: './app-item.component.html',
  styleUrls: ['./app-item.component.css']
})
export class AppItemComponent {
  @Input() item:AppListItem;
  state: string = 'normal';

  @HostListener('touchstart') onPress() {
    this.state = 'pressed';
    //console.log("mouse down");
  }

  @HostListener('touchend') onRelease() {
    this.state = 'normal';
    //console.log("mouse up");
  }

}



